import SwiperCore from 'swiper/core'
import 'swiper/swiper-bundle.css'
// import singlePrice from '~/components/variable/singlePrice'

export default {
  /* components: {
    singlePrice,
  }, */
  mounted() {
    this.swiper = new SwiperCore('.ticker-carousel', {
      loop: false,
      slidesPerView: 1,
      spaceBetween: 15,
      breakpoints: {
        540: {
          slidesPerView: 2,
        },
        1024: {
          slidesPerView: 3,
        },
        1280: {
          slidesPerView: 4,
        },
        1600: {
          slidesPerView: 5,
        },
        1800: {
          slidesPerView: 6,
        },
      },
    })
  },
}
